@import '../../style-config/mixins.scss';
@import '../../style-config/variable.scss';

.call_to_Action{
    height: 47px;
    width: fit-content;
    padding: 0 15px;
    background: $white;
    display: flex;
    align-items: center;
    justify-content: center;
    color: $black-light;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    border-radius: 8px;
    @include typography(18px,400,1);
}